              </span>
        </a-table>
      </a-form-model-item>
      <a-form-model-item label="">
        <a-button type="link" icon="plus" @click="paramResModel"></a-button>
        <a-table
          :rowKey="(record, index) => {return index}"
          style="margin-top: 15px;"
          :columns="resParaColumns"
          :data-source="resParsdata">
           <span slot="dataType" slot-scope="text, record">
            <span v-for="(item, index) in stateArr" v-if="item.value === text" :key="index">
              {{item.title}}
            </span>
          </span>
              <span slot="action" slot-scope="text, record, index">
             <span class="table-btn" @click="putCommandResParam(record, index)"></span>
             <span class="table-btn" @click="deleteCommandResParams(record)"></span>
              </span>
        </a-table>
      </a-form-model-item>
    </a-form-model>
