    >
      <a-form-model-item ref="propertyLabel" label="" prop="paramLabel">
        <a-input v-model="commandPropertyForm.paramLabel" placeholder=""/>
      </a-form-model-item>
      <a-form-model-item label="">
        <BTextarea
          class="full-width"
          style="height: auto"
          v-model="commandPropertyForm.description"
          placeholder=""
        />
      </a-form-model-item>
      <a-form-model-item label="" prop="dataType">
        <a-select v-model="commandPropertyForm.dataType" placeholder="">
          <a-select-option v-for="(item, index) in stateArr" :value="item.value" :key="index">
            {{item.title}}
          </a-select-option>
        </a-select>
      </a-form-model-item>
<!--      <a-form-model-item label="" prop="minValue">
        <a-input-number placeholder="" v-model="commandPropertyForm.minValue" :style="{ display: 'inline-block', width: 'calc(50% - 12px)' }"/>
        <span :style="{ display: 'inline-block', width: '24px', textAlign: 'center' }">
        -
      </span>
        <a-input-number placeholder="" v-model="commandPropertyForm.maxValue" :style="{ display: 'inline-block', width: 'calc(50% - 12px)' }"/>
      </a-form-model-item>-->
    </a-form-model>
  </a-modal>
<!--  -->
  <a-modal :mask="false" :maskClosable="false" v-model="modelVisible" :width="600" :title="modelTitle"
           @cancel="closeModel"
           @ok="modelHandleOk">
    <a-form-model
      ref="modelForm"
      :model="modelForm"
      :rules="modelRules"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-form-model-item ref="modelLabel" label="" prop="modelLabel">
        <a-input v-model="modelForm.modelLabel" placeholder=""/>
      </a-form-model-item>
      <a-form-model-item ref="modelType" label="">
        <a-input v-model="modelForm.modelType" placeholder=""/>
      </a-form-model-item>
      <a-form-model-item label="">
        <BTextarea
          class="full-width"
          style="height: auto"
          v-model="modelForm.description"
          placeholder=""
        />
      </a-form-model-item>
    </a-form-model>
  </a-modal>
