<template>
<div>
  <p>
    MQTTTopic
  </p>
  <a-table :columns="columns" :data-source="data" bordered>
    <template slot="name" slot-scope="text">
      <a>{{ text }}</a>
    </template>
  </a-table>
</div>
</template>

<script>
const renderContent = (value, row, index) => {
  const obj = {
    children: value,
    attrs: {}
  }
  return obj
}
const data = [
  {
    key: '1',
    topicName: 'OTA',
    topic: '$oc/ota/response',
    publisher: '',
    receiver: '',
    effect: 'OTAOTA'
  },
  {
    key: '2',
    topicName: 'OTA',
    topic: '$oc/ota/request',
    publisher: '',
    receiver: '',
    effect: 'OTA '
  },
  {
    key: '3',
    topicName: '',
    topic: '$oc/properties/up',
    publisher: '',
    receiver: '',
    effect: ''
  },
  {
    key: '4',
    topicName: '',
    topic: '$oc/properties/get',
    publisher: '',
    receiver: '',
    effect: ''
  },
  {
    key: '5',
    topicName: '',
    topic: '$oc/messages/up',
    publisher: '',
    receiver: '',
    effect: 'json,json payloadcontent '
  },
  {
    key: '6',
    topicName: '',
    topic: '$oc/config/request',
    publisher: '',
    receiver: '',
    effect: ''
  },
  {
    key: '7',
    topicName: '',
    topic: '$oc/config/response',
    publisher: '',
    receiver: '',
    effect: ''
  },
  {
    key: '8',
    topicName: '',
    topic: '$oc/control/request',
    publisher: '/',
    receiver: '/',
    effect: ''
  },
  {
    key: '9',
    topicName: '',
    topic: '$oc/control/response',
    publisher: '/',
    receiver: '/',
    effect: ''
  },
  {
    key: '10',
    topicName: '',
    topic: '$oc/warn/self',
    publisher: '/',
    receiver: '/',
    effect: ' '
  }
]

export default {
  name: 'TopicList',
  data() {
    const columns = [
      {
        title: 'Topic',
        dataIndex: 'topicName',
        customRender: (value, row, index) => {
          const obj = {
            children: value,
            attrs: {}
          }
          if (index === 0) {
            obj.attrs.rowSpan = 2
          }
          if (index === 1) {
            obj.attrs.rowSpan = 0
          }
          if (index === 2) {
            obj.attrs.rowSpan = 2
          }
          if (index === 3) {
            obj.attrs.rowSpan = 0
          }
          if (index === 5) {
            obj.attrs.rowSpan = 2
          }
          if (index === 6) {
            obj.attrs.rowSpan = 0
          }
          if (index === 7) {
            obj.attrs.rowSpan = 2
          }
          if (index === 8) {
            obj.attrs.rowSpan = 0
          }
          return obj
        }
      },
      {
        title: 'topic',
        dataIndex: 'topic',
        customRender: renderContent
      },
      {
        title: '',
        dataIndex: 'publisher',
        customRender: renderContent
      },
      {
        title: '',
        dataIndex: 'receiver',
        customRender: renderContent
      },
      {
        title: '',
        dataIndex: 'effect',
        customRender: renderContent
      }
    ]
    return {
      data,
      columns
    }
  }
}
</script>

<style scoped>

</style>



